<template>
    <div>
        <StandardHeader :search="search"></StandardHeader>
        <div>
            <img src="assets/img/coming-soon.jpg" id="coming-soon"/>
        </div>
    </div>
</template>

<script>
// Library components.
// Custom components.
import StandardHeader from './StandardHeader.vue'
import router from '../router'
import { mapState } from 'vuex'

export default {
    name: 'Home',
    components: {
        StandardHeader
    },

    data() {
        return {
            loading: false
        }
    },
    created: function() {
        //this.search()
    },

    methods: {
        search() {
            router.push({ path: `/SearchResults` }) 
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    #coming-soon{
        margin-top: 100px;
        margin-left: auto;
        margin-right: auto;
        display: block;
    }
</style>



